import { Component } from '@angular/core';
import {TimerObservable} from 'rxjs/observable/TimerObservable';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  options: Object;

  public constructor() {
    this.options = {
      chart: {
        width: 900,
        height: 550,
      },
      navigator: {
        enabled: true,
      }
    };
    this.options['series'] = [{
      data: [
        [ 0, 0 ],
      ]
    }];
  }

  public onLoad(event): void {
    // set up the updating of the chart each second
    const series = event.context.series[0];
    const timer = TimerObservable.create(0, 1000);

    timer.subscribe(x => {
      let y = 0;
      const random = Math.random();
      if (random % 2 === 0) {
        y = y + Math.random() * 10000;
      } else {
        y = y - Math.random() * 10000;
      }
      series.addPoint([x * 1000, y]);
    });
  }
}



